body.dark {
  background-color: #343a40 !important;
  color: #fff !important;
}
body.dark input.form-control,
body.dark select.form-control,
body.dark textarea,
body.dark table,
body.dark table th,
body.dark table td,
body.dark div.card,
body.dark div.card .card-header,
body.dark div.card .card-footer,
body.dark aside,
body.dark nav.card,
body.dark ul.pagination li.page-item a.page-link,
body.dark footer {
  background-color: #343a40 !important;
  color: #fff !important;
}
body.dark .dropdown-menu {
  border: 1px solid #fff !important;
  background-color: #343a40 !important;
  color: #fff !important;
}
body.dark a, body.dark i.bi {
  color: #fff !important;
}
body.dark .dropdown-item:hover {
  background-color: #231955 !important;
}
body.dark ul.pagination li.page-item.active a.page-link {
  color: #212529 !important;
  background-color: #fff !important;
  border-color: transparent;
}

body {
  overflow-x: hidden;
}